import { Brain } from 'lucide-react'

const footerSections = [
  {
    title: 'Product',
    links: ['Features', 'Pricing', 'Use Cases', 'Integrations'],
  },
  {
    title: 'Resources',
    links: ['Blog', 'Help Center', 'Tutorials', 'API Docs'],
  },
  {
    title: 'Company',
    links: ['About Us', 'Careers', 'Privacy Policy', 'Terms of Service'],
  },
]

export default function Footer() {
  return (
    <footer className="bg-gray-900 text-white">
      <div className="container mx-auto px-6 py-12">
        <div className="grid md:grid-cols-2 lg:grid-cols-5 gap-8">
          {/* Brand */}
          <div className="lg:col-span-2">
            <div className="flex items-center space-x-2 mb-4">
              <div className="p-2 bg-gradient-to-r from-blue-500 to-purple-600 rounded-lg">
                <Brain className="h-6 w-6 text-white" />
              </div>
              <span className="text-xl font-bold">Memorae AI</span>
            </div>
            <p className="text-gray-400 mb-6 max-w-md">
              Transforming how the world preserves and rediscovers memories through artificial intelligence.
            </p>
            <div className="flex space-x-4">
              {['Twitter', 'Facebook', 'Instagram', 'LinkedIn'].map((social) => (
                <a
                  key={social}
                  href="#"
                  className="w-10 h-10 bg-gray-800 rounded-lg flex items-center justify-center hover:bg-gray-700 transition-colors"
                >
                  {social[0]}
                </a>
              ))}
            </div>
          </div>

          {/* Links */}
          {footerSections.map((section) => (
            <div key={section.title}>
              <h3 className="font-semibold text-lg mb-4">{section.title}</h3>
              <ul className="space-y-2">
                {section.links.map((link) => (
                  <li key={link}>
                    <a
                      href="#"
                      className="text-gray-400 hover:text-white transition-colors"
                    >
                      {link}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </div>

        <div className="border-t border-gray-800 mt-12 pt-8 text-center text-gray-400">
          <p>&copy; 2024 Memorae AI. All rights reserved.</p>
        </div>
      </div>
    </footer>
  )
}